import { useWindowSize } from '../../hooks/useWindowSize';
import {
  MIN_WINDOW_WIDTH_FOR_ROW_FLEX
} from '../../container/constants'

const StarvationInfoText = () => {
  const windowSize = useWindowSize();
  const isNotMobile = windowSize.width > MIN_WINDOW_WIDTH_FOR_ROW_FLEX;
  const textFontSize = isNotMobile ? 30 : 16
  const titleFontSize = textFontSize * 1.5

  return (
    <div style={{display: 'flex', flexDirection: 'column', margin: 20}}>
      <h2 className="dohyeon-font-text" style={{marginBottom: 0, fontSize: titleFontSize}}>그 소식 들으셨어요?</h2>
      <h2 className="dohyeon-font-text" style={{marginTop: 0, fontSize: titleFontSize}}>소식(少食)하면… 누군가는 배고픔을 해결할 수 있대요!</h2>
      <p className="jua-font-text" style={{fontSize: textFontSize}}>지구에 현재 전 세계의 모든 사람들이 먹을 수 있는 만큼의 충분한 음식이 있다는 사실을 아시나요? 하지만 전체 음식의 1/3 정도의 양은 섭취되지 못한 채 버려지거나, 운송/보관 과정에서 썩는다고 합니다.</p>
      <p className="jua-font-text" style={{fontSize: textFontSize}}>충분한 음식이 있는데도 불구하고 전세계 약 7억 명의 사람들이 굶주린 채 하루를 마무리하는 것입니다.</p>
    </div>
  );
};

export default StarvationInfoText;

