import { useState } from 'react'
import * as d3 from 'd3';

import { useD3 } from '../../hooks/useD3';
import { useWindowSize } from '../../hooks/useWindowSize';
import {
  MIN_WINDOW_WIDTH_FOR_ROW_FLEX,
  MOBILE_WIDTH
} from '../../container/constants';
import office from './office.svg';
import wfpEmblem from './wfp-logo-emblem-white.svg';
import money from './money.png';

const SERVICE_INFO_VIZ_D3_CLIP_PATH_ID = 'service-info-viz-d3-clip-path-id';

const ServiceInfoViz = () => {
  const [donation, setDonation] = useState(0);
  const windowSize = useWindowSize();
  const reactiveSize = windowSize.width / 2 - 100
  const isNotMobile = windowSize.width > MIN_WINDOW_WIDTH_FOR_ROW_FLEX;
  const dims = isNotMobile ? {
    height: reactiveSize * 0.75,
    width: reactiveSize,
  } : {
    height: MOBILE_WIDTH * 0.75,
    width: MOBILE_WIDTH
  }
  
  const plateRadius = dims.width / 6;
  const foodRadius = plateRadius * 0.75;

  const center = {
    x: plateRadius,
    y: dims.height / 2
  };
  
  const clipOffDims = {
    x: center.x - foodRadius,
    y: center.y - foodRadius,
    height: foodRadius * 2,
    // width: foodRadius * 2 * 0.65
    width: foodRadius * 2
  };

  const twoThirdsWidth = dims.width / 3 * 2;
  const officeHeight = plateRadius * 2;
  const officeDims = {
    x: twoThirdsWidth,
    y: center.y - officeHeight / 2,
    height: officeHeight
  };

  const moneyWidth = plateRadius * 1.5;
  const moneyHeight = moneyWidth / 2;
  const moneyDims = {
    x: (dims.width / 2) - (moneyWidth / 2),
    y: (dims.height / 2) - (moneyHeight / 2),
    width: moneyWidth,
    height: moneyHeight
  };

  const ref = useD3(
    (svg) => {

      const onDrag = (event) => {
        const maxRectWidth = foodRadius * 2;
        const minRectWidth = foodRadius * 2 * 0.65
    
        const clipRect = svg.select(`#${SERVICE_INFO_VIZ_D3_CLIP_PATH_ID} rect`);
        
        let newRectWidth = 0;
        const candidate = Number.parseInt(clipRect.attr('width'), 10) + event.dx;
        if (candidate > maxRectWidth) 
          newRectWidth = maxRectWidth;
        else if (candidate < minRectWidth)
          newRectWidth = minRectWidth
        else
          newRectWidth = candidate
        
        const auxVar = 10000 / 7;
        
        const newDonation = Math.round((-1 * auxVar * newRectWidth / foodRadius) + (2 * auxVar));

        clipRect.attr('width', newRectWidth)
        setDonation(newDonation);
      };

      const drag = d3.drag()
        .on("drag", onDrag);

      svg.select(`#plate`).call(drag);
    },
    [windowSize.width]
  );
    
  return (
    <div>
      <svg ref={ref} width={dims.width} height={dims.height}>
        <defs>
          <clipPath id={SERVICE_INFO_VIZ_D3_CLIP_PATH_ID}>
            <rect x={clipOffDims.x} y={clipOffDims.y} height={clipOffDims.height} width={clipOffDims.width} />
          </clipPath>
        </defs>

        <circle id='plate' cx={center.x} cy={center.y} r={plateRadius} strokeWidth='0' fill='white' />
        <circle id='food' cx={center.x} cy={center.y} r={foodRadius} strokeWidth='0' fill='orange' clipPath={`url(#${SERVICE_INFO_VIZ_D3_CLIP_PATH_ID})`} />
        <text
          transform={`translate(${center.x}, ${center.y})`}
          textAnchor="middle"
          alignmentBaseline="middle"
          fontSize={isNotMobile ? 25 : 16}
          fontWeight="bold"
        >줄여주세요</text>
        
        <image
          xlinkHref={office}
          x={officeDims.x}
          y={officeDims.y}
          height={officeDims.height}
        />

        <g>
          {/* <rect id='money' x={moneyDims.x} y={moneyDims.y} width={moneyDims.width} height={moneyDims.height} fill='#85bb65' /> */}
          <image
            xlinkHref={money}
            x={moneyDims.x}
            y={moneyDims.y}
            width={moneyDims.width}
            height={moneyDims.height}
          />
          <text
            transform={`translate(${moneyDims.x + (moneyDims.width / 2)}, ${moneyDims.y + (moneyDims.height / 2)})`}
            textAnchor="middle"
            alignmentBaseline="middle"
            fontSize={isNotMobile ? 25 : 16}
            fontWeight="bold" 
          >
            ₩{donation}
          </text>
        </g>

        {/* <image
          xlinkHref={wfpEmblem}
          x='200'
          y={dims.height / 2}
          width='200'
        /> */}
      </svg>
    </div> 
  );
};

export default ServiceInfoViz;
