import { useWindowSize } from '../../hooks/useWindowSize';
import {
  MIN_WINDOW_WIDTH_FOR_ROW_FLEX
} from '../../container/constants'

const HealthInfoText = () => {
  const windowSize = useWindowSize();
  const isNotMobile = windowSize.width > MIN_WINDOW_WIDTH_FOR_ROW_FLEX;
  const textFontSize = isNotMobile ? 30 : 16
  const titleFontSize = textFontSize * 1.5

  return (
    <div style={{display: 'flex', flexDirection: 'column', margin: 20}}>
      <h2 className="dohyeon-font-text" style={{marginBottom: 0, fontSize: titleFontSize}}>그 소식 들으셨어요?</h2>
      <h2 className="dohyeon-font-text" style={{marginTop: 0, fontSize: titleFontSize}}>소식(少食)하면… 건강해진대요!</h2>
      <p className="jua-font-text" style={{fontSize: textFontSize}}>미국 위스콘신대학교 매디슨 캠퍼스 연구팀과 미국국립노화연구소 연구팀은 열량 섭취를 제한함으로써 더 건강하게 장수를 누릴 수 있는 방법이라고 발표했습니다. 하지만 잦은 외식을 하다 보면 메뉴의 정량 때문에 의도치 않게 많은 음식을 먹거나 음식을 남기는 경우가 있는데요.</p>
      <p className="jua-font-text" style={{fontSize: textFontSize}}>결국 본인의 식사량보다 많은 음식을 구매하면서, 장기적으로 건강까지 해치는 꼴이 되는 겁니다.</p>
    </div>
  );
};

export default HealthInfoText;
