import Carousel from 'react-material-ui-carousel';
import SimpleContainer from '../../container/SimpleContainer';
import CampaignInfoSlide from './CampaignInfoSlide';

import cardNews1 from './cardNews1.png'
import cardNews2 from './cardNews2.png'
import cardNews3 from './cardNews3.png'
import cardNews4 from './cardNews4.png'
import cardNews5 from './cardNews5.png'

const images = [
  cardNews1,
  cardNews2,
  cardNews3,
  cardNews4,
  cardNews5
];

const CampaignInfoSlider = ({ enabled }) => {
  const slides = images.map((img, i) => 
    <CampaignInfoSlide 
      key={`campaign-info-slide-${i}`} 
      img={img} 
    />)

  if (enabled)
    return (
      <div style={{marginTop: 20, marginBottom: 20}}>
        <Carousel
          autoPlay={false}
        >
          {slides}     
        </Carousel>
      </div>
      
    );
  else
    return (
      <SimpleContainer flex={1} flexDirection={'column'}>
        {slides}
      </SimpleContainer>
    );
}

export default CampaignInfoSlider;
